<template>
  <div>
    <el-table :data="tableData" stripe style="width: 100%">
      <el-table-column prop="name" label="分类名">
        <template slot-scope="scope">
          <template v-if="scope.row.edit">
            <el-input v-model="scope.row.name" style="width: 200px" size="small" placeholder=""></el-input>
            <el-button @click="confirm(scope.row)" size="small">确定</el-button>
          </template>
          <template v-else>
            {{scope.row.name}}
          </template>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="100">
        <template slot-scope="scope">
          <el-button @click="edit(scope.row)" type="text" size="small">编辑</el-button>
          <el-button @click="handleDelete(scope.row)" type="text" size="small">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <el-tooltip class="item" effect="dark" content="新增" placement="top">
      <el-button class="fixedaddbutton" icon="el-icon-plus" type="primary" circle @click="add"></el-button>
    </el-tooltip>
  </div>
</template>

<script>
  import Channel from "@/request/Channel"
  const channelRequest = new Channel();
  export default {
    async created() {
      let res = await channelRequest.getAll();
      for (const item of res.rows) {
        item.edit = false
        this.tableData.push(item)
      }
    },
    data() {
      return {
        tableData: [ ]
      }
    },
    methods: {
      async add() {
        let name = window.prompt("请输入名称","")
        if(!name) return false
        let data = {
          name
        }
        let res = await channelRequest.create(data)
        if(res.statusCode) {
          alert(res.message)
          return false
        }
        if(res.id) {
          this.tableData.push(res)
        }
      },
      edit(row) {
        this.name = row.name
        row.edit = true
      },
      async handleDelete(row) {
        let delelteIt = window.confirm("确认删除?");
        if(!delelteIt) return false
        let res = await channelRequest.deleteById(row.id)
        if(!res) return false
        let index = this.tableData.findIndex(item => item.id == row.id)
        
        if(index == -1) return false
        this.tableData.splice(index, 1);
      },
      async confirm(row) {
        
        let res = await channelRequest.updateById(row.id, {
          name: row.name
        })
        if(res.statusCode) {
          alert(res.message)
          return false
        }
        if(res) {
          row.edit = false
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  .main {}
</style>